
<template>
    <div>
        <mt-header title="" fixed class="mt-header wid">
            <router-link to="Myhome" slot="left">
                <mt-button icon="back"></mt-button>
            </router-link>
        </mt-header>

        <div class="ab-tit"></div>
        <div class="card">
            <img src="img/card.jpg" alt="">
        </div>
        <hr>
        <div class="waring">
            &nbsp;<h4>:</h4>
            <p>&nbsp;&nbsp;</p>
        </div>
        <hr>
        <form action="uploadFile?did=0"  method="POST" enctype="multipart/form-data" @submit='uploadimg'>
            <input type="file" name="mypic" class="upload" id='img'>
            <br>
            <br>
            <input type="submit" value="" class="submit">
        </form>
        <!-- <div>
            <input type="file" name="mypic" class="upload" id="img">
            <br>
            <input type="button" value="" class="submit" @click="uploadimg">
        </div> -->
    </div>
</template>
<script>

import {Toast} from 'mint-ui'
export default {
    data(){ return {  } },
    methods:{
        uploadimg(){
            let img=document.getElementById('img').files[0]

            if(img){
                Toast('')
            }else{
                e.preventDefault()
                Toast('')
            }
            // let x=document.getElementById('img').files[0];
            // console.log(x);
            // let params = new FormData() ; //form
            // params.append('file',x);  //append form
            // //  formmultipart/form-data
            // let config = {
            //     headers:{'Content-Type':'multipart/form-data'}
            // }
            // console.log(params);
            // this.axios.post("uploadFile",params,config)
            //     .then((result)=>{
            //     console.log(result);
            // })
        }
    }
}
</script>


<style>
    form{
        padding: 0 10px;
    }
    .submit{
        margin-left: 70%;
    }
    .card>img{
        width: 80%;
        display: block;
        margin: 0 auto;
    }
    .waring{
        padding:0 10px;
    }
    .waring>p{
        color: rgb(165,42,42)
    }
</style>








